import {action, observable} from 'mobx-angular';
import {Injectable} from '@angular/core';
import {IVoucherRequest} from "../model/voucher.model";

@Injectable({providedIn: 'root'})
export default class VoucherRequestStore {
  @observable voucherRequests: IVoucherRequest[] = [];

  constructor() {
    this.voucherRequests = [];
  }

  @action
  async setVoucherRequests(voucherRequests: IVoucherRequest[]){
    console.log('Setting voucher requests: ', voucherRequests);
    this.voucherRequests.push(...voucherRequests);
  }

  @action
  addVoucherRequest(voucherRequest: IVoucherRequest){
    console.log('Setting voucher request: ', voucherRequest);
    this.voucherRequests.unshift(voucherRequest);
  }




}
